
.btnShowdata{
    display: block;
    height:40px;
    margin: 0 auto;
    margin-top: 2vh;
    line-height: 14px * 2;
    padding: 0 20px;
    background:#e9798f;
    letter-spacing: 2px;
    transition: .2s all ease-in-out;
    outline: none;
    border: 1px solid rgba(0,0,0,1);
    box-shadow: 3px 3px 1px #ac82b6, 3px 3px 1px 1px rgba(0,0,0,1);
}


.btnShowdata:hover{
    background: rgba(0,0,0,1);
    color: white;
    border: 1px solid rgba(0,0,0,1);
}

.btnShowdata:hover > a{
    color:white;
}

a:hover{
    color:white;
}

a { text-decoration: none; color: black; }
a:visited { text-decoration: none; }
a:hover { text-decoration: none; }
a:focus { text-decoration: none; }
a:hover, a:active { text-decoration: none; }
